var usrMenus = 'user-message'
var menuState = 0;
var activeClassName = "user-menu.visible"
var menuPosition;
var menuPositionX;
var menuPositionY;
var menuItemInContext;

function init () {
    contextListener();
    clickListener();
    keyupListener();
}

function contextListener () {
    var menuItems = document.querySelectorAll('.user-message');
    for (var i = 0; i < menuItems.length; i++) {
        var e = menuItems[i];
        var openMenu = function (e) {
            if (clickInsideElement(e, usrMenus)) {
                e.preventDefault();
                toggleMenuOn(this);
                positionMenu(this);
            } else {
                toggleMenuOff(this);
            }
        }
        e.addEventListener("contextmenu", openMenu);
    }
}

function clickListener () {
    var menuItems = document.querySelector('.user-message');
    for (var i = 0; i < menuItems.length; i++) {
        var el = menuItems[i];
        var closeMenu = function () {
            var button = this.which || this.button;
            if (button === 1) {
                toggleMenuOff(this);
            }
        }
        document.addEventListener("click", closeMenu);
    }
}

function keyupListener () {
    window.onkeyup = function (e) {
        if (e.keyCode === 27) {
            this.toggleMenuOff(menu);
        }
    }
}

function clickInsideElement (e, className) {
    var el = e.srcElement || e.target;
    if (el.classList.contains(className)) {
        return el;
    } else {
        while (el = el.parentNode) {
            if (el.classList && el.classList.contains(className)) {
                return el;
            }
        }
    }
    return false;
}

function toggleMenuOn (item) {
    if (menuState !== 1) {
        menuState = 1;
        item.nextElementSibling.classList.add('visible')
    }
}

function toggleMenuOff (item) {
    if (menuState !== 0) {
        menuState = 0;
        item.nextElementSibling.classList.remove('visible')
    }
}

function getPosition (e) {
    var posx = 0;
    var posy = 0;

    if (!e) var e = window.event;
    if (e.pageX || e.pageY) {
        posx = e.pageX;
        posy = e.pageY;
    } else if (e.clientX || e.clientY) {
        posx = e.clientX + document.body.scrollLeft + document.documentElement.scrollLeft;
        posy = e.clientY + document.body.scrollTop + document.documentElement.scrollTop;
    }
    return {
        x: posx,
        y: posy
    }
}

function positionMenu (e) {
    menuPosition = getPosition(e);
    menuPositionX = menuPosition.x + "px";
    menuPositionY = menuPosition.y + "px";
    e.style.left = menuPositionX;
    e.style.top = menuPositionY;
}

init();